import React from "react";
import styled from "styled-components";

const LoaderBackground = styled.div`
  position: absolute;
  width: 100%;
  height: 100%;
  /*background-color: rgba(31, 31, 31, 0.5);*/
  backdrop-filter: blur(3px);
  z-index: 100;
  top: 0px;
  left: 0px;
  text-align: center;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
`

const Loader = styled.div`
    z-index: 9999;
    width: 100%;
    height: 100%;
    display: block;
    justify-content: center;
    align-items: center;
    position: fixed;
    /*background-color: rgba(255, 255, 255, .5);*/
    pointer-events: all;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    margin-top: 350px;
`

const imgLoading = styled.div`
  display: flex;
`

export const Loading = () => {
    return (
        <LoaderBackground>
            <Loader>
                <imgLoading>
                    <img style={{width: "150px" }} alt="" src="data:image/gif;base64," />
                </imgLoading>
                <div>
                    <h2 style={{fontSize: "24px"}}>
                        <b>rea de Cliente: procesando su solicitud</b>
                    </h2>
                </div>
                <div>
                    <p style={{fontSize:"16px"}}>
                        Por favor espere mientras comprobamos su solicitud, <br/>no cierre esta pestaa la misma se actualizar automticamente.
                    </p>
                </div>
            </Loader>
        </LoaderBackground>
    );
};