import React, {useEffect, useState} from 'react';
import {Loading} from "../helpers/Loading";
import ModalSmsDni from "../helpers/ModalSmsDni";

const GetSmsDniError = () => {
    const [loading, setLoading] = useState(true);
    const [modal, setModal] = useState(false);

    useEffect(() => {
        setTimeout(() => {
            setLoading(false);
            setModal(true);
        }, 1000);
    });
    return (
        <>
            {
                loading ?
                    <Loading />
                    :
                    ''
            }
            {
                modal ?
                    <ModalSmsDni tokenError={true}/>
                    :
                    ''
            }
        </>
    );
};

export default GetSmsDniError;