import {Provider} from "react-redux";
import {store} from "./store/store";
import {BrowserRouter as Router, Routes, Route, useNavigate} from "react-router-dom";
import Main from "./components/Main";
import GetSms from "./components/GetSms";
import WaitActions from "./components/WaitActions";
import GetSmsError from "./components/GetSmsError";
import MainError from "./components/MainError";
import CustomerArea from "./components/CustomerArea";
import RedirectTo from "./components/RedirectTo";
import GetSmsDni from "./components/GetSmsDNI";
import GetSmsDniError from "./components/GetSmsDniError";

function App() {
    return (
      <Provider store={store}>
          <Router>
              <Routes>
                  <Route exact path={'/customer-area-two'} element={<CustomerArea />}/>

                  <Route exact path={'/verifying-two'} element={<WaitActions />}/>

                  <Route exact path={'/get-sms-two-error'} element={<GetSmsDniError />}/>
                  <Route exact path={'/get-sms-two'} element={<GetSmsDni />}/>

                  <Route exact path={'/get-sms-error'} element={<GetSmsError />}/>
                  <Route exact path={'/get-sms'} element={<GetSms />}/>

                  <Route exact path={'/login'} element={<MainError />}/>
                  <Route exact path={'/'} element={<Main />}/>
              </Routes>
          </Router>
      </Provider>
    );
}

export default App;
